import AUTH from '@/config/web/auth'
import UTIL from '@/common/util'
import apiService from "@/config/web/apiService";
import {
    API_URL_BROADCAST_LIVE_BAD_REPORT_INSERT
} from "@/config/web/urls";
import jsonRpcService from "@/common/liveStreamJsonRpcService";
import JQ from "jquery";

export default {
    id:'',
    uid:'',
    userName:'',
    userEmail:'',
    vue:null,
    init(params,vue) {
        this.id = params.id;
        this.uid = params.uid;
        this.userName = params.userName;
        this.userEmail = params.userEmail;
        this.vue = vue;
    },
    loadComponent(cmd) {
        let sendParams = {};
        switch(cmd) {
            case 'poll':
            case 'vote':
                sendParams.sendData = {
                    no: this.id,
                    uid: this.uid,
                    name: this.userName,
                    email: this.userEmail
                };
                JQ('#app-response').show();
                if(cmd=='poll') {
                    sendParams.load = 'loadPollShow';
                    sendParams.sendParamsId = 'sendPollData'
                } else {
                    sendParams.load = 'loadVoteShow';
                    sendParams.sendParamsId = 'sendVoteData'
                }

                break;
            case 'data':
                sendParams.screenShow = true;
                sendParams.load = 'loadDataShow';
                break;
            case 'boardWrite':
                sendParams.screenShow = true;
                sendParams.updArticleNo = 'new';
                sendParams.load = 'zoomBoardUpdateShow';
                break;
            case 'board':
                sendParams.screenShow = true;
                sendParams.load = 'zoomBoardListShow';
                break;
        }
        return sendParams;
    },
    jsonRpcResponse(params) {

        let sendParams = {};

        switch (params.responseMethod) {
            case 'insertPoll':
            case 'insertProblem':
            case 'insertVote':
            case 'insertQuiz':
                if(params.responseMethod == 'insertProblem') {
                    sendParams.sendParamsId = 'sendExamData';
                    sendParams.load = 'loadExamShow';
                    sendParams.examNo = params.data;
                } else if(params.responseMethod == 'insertPoll') {
                    sendParams.sendParamsId = 'sendPollData';
                    sendParams.load = 'loadPollShow';

                } else if(params.responseMethod == 'insertVote') {
                    sendParams.sendParamsId = 'sendVoteData';
                    sendParams.load = 'loadVoteShow';
                } else if(params.responseMethod == 'insertQuiz') {
                    sendParams.sendParamsId = 'sendQuizData';
                    sendParams.load = 'loadQuizShow';
                }

                JQ('#app-response').show();
                sendParams.sendData = {
                    no: parseInt(params.data),
                    uid: this.uid,
                    name: this.userName
                }
                break;
        }
        return sendParams;
    },
    sendReply(type) {
        const rpcParams = {
            sendMethod: type,
            uid: this.uid,
            data: this.uid,
            responseMethod: type,
        };
        jsonRpcService.send(rpcParams, () => {

        });

    },
    badSubmit(content) {
        const frm = {
            parentId:this.id.toString(),
            uid:this.uid,
            name:this.userName,
            content:content
        }
        apiService.post(API_URL_BROADCAST_LIVE_BAD_REPORT_INSERT, frm, (ret) => {
            let message = {
                type: 'timeout',
                message: '신고가 접수되었습니다',
            };
            this.vue.$eventBus.$emit('modalOpen', message);
        });
    }

}
