import AUTH from '@/config/web/auth'
import UTIL from '@/common/util'
import apiService from "@/config/web/apiService";
import {
    API_URL_BROADCAST_USER_LIVE_WITH_STATUS_ING,
    API_URL_BROADCAST_LIVE_USER_JOIN,
    API_URL_BROADCAST_LIVE_USER_OUT,
    API_URL_BROADCAST_UPDATE_USER_STATUS,
    API_URL_BROADCAST_LIVE_GET_EXAM
} from "@/config/web/urls";
import jsonRpcService from "@/common/liveStreamJsonRpcService";
import JQ from "jquery";


export default {
    DEBUG: false,
    id: '',
    uid: '',
    userName: '',
    userEmail: '',
    streamRoomId: '',
    liveIngTimeKey: '',
    liveIngTime: '',
    vue: null,
    init(params, vueObj, debug) {
        this.DEBUG = debug;
        this.id = params.id;
        this.uid = params.uid;
        this.userName = params.userName;
        this.userEmail = params.userEmail;
        this.vue = vueObj;

    },
    getLiveInfo(func) {
        const frm = {id: this.id, uid: this.uid}
        apiService.post(API_URL_BROADCAST_USER_LIVE_WITH_STATUS_ING, frm, (ret) => {
            this.streamRoomId = this.id + '_' + ret.info.uid;
            if (ret.info.start_time) {
                const nowDate = new Date(ret.info.start_time);
                this.liveIngTime = parseInt(nowDate.getTime());

            }

            func(ret);

        })


    },
    accessRoom() {
        this.joinSocketRoom();
    },
    setStreamRoomId(streamRoomId) {
        this.streamRoomId = streamRoomId;
    },
    joinSocketRoom() {
        const accessType = 'no';
        let job = (sessionStorage.getItem('userJob')) ? sessionStorage.getItem('userJob') : '';
        let part = (sessionStorage.getItem('userPart')) ? sessionStorage.getItem('userPart') : '';
        let posi = (sessionStorage.getItem('userPosi')) ? sessionStorage.getItem('userPosi') : '';
        let status = 'ing';
        const frm = {
            job: job,
            part: part,
            posi: posi,
            parentId: this.id.toString(),
            name: this.userName,
            email: this.userEmail,
            uid: this.uid,
            status: status,
        }

        apiService.post(API_URL_BROADCAST_LIVE_USER_JOIN, frm, (ret) => {
            let params = {
                uid: this.uid,
                name: this.userName,
                email: this.userEmail,
                roomId: this.streamRoomId,
                accessType: accessType,
                roomHost: 'no',
            }
            jsonRpcService.init(params, this.vue, this.DEBUG);
        });

    },
    updateUserStatusWithIng() {
        const frm = {
            parentId: this.id.toString(),
            uid: this.uid,
            status: 'ing',
        }

        apiService.post(API_URL_BROADCAST_UPDATE_USER_STATUS, frm, (ret) => {

        });
    },
    getStartTime() {
        let ingTime = parseInt(this.liveIngTime);
        let nowDate = new Date();
        let timeCnt = (nowDate.getTime() - ingTime) / 1000;
        timeCnt = Math.floor(timeCnt);
        if (timeCnt < 0) timeCnt = 0;
        return timeCnt;
    },
    updateLiveTime() {

    },
    beforeUnload() {
        jsonRpcService.close("user");
        this.updateUserStatusWithIng();
    },
    liveTimeRemove() {

    },
    setTimeVal(sec) {
        sec = parseInt(sec);
        if (isNaN(sec)) return '00:00:00';
        if (sec < 0) return '00:00:00';
        let resultSec = sec % 60;
        resultSec = Math.floor(resultSec)
        let min = sec / 60;
        min = Math.floor(min);
        let resultMin;
        if (min >= 60) {
            resultMin = min % 60;
        } else {
            resultMin = min;
        }
        resultMin = Math.floor(resultMin);
        let hour = min / 60;
        hour = Math.floor(hour);
        let resultHour;
        let day = '';
        if (hour >= 24) {
            resultHour = hour % 24;
        } else {
            resultHour = hour;
        }
        resultHour = Math.floor(resultHour);
        return day + this.zf(resultHour) + ':' + this.zf(resultMin) + ':' + this.zf(resultSec);
    },
    zf(date) {
        date = parseInt(date);
        if (date < 10) {
            return "0" + date;
        } else return date;
    },
    doEndMeetingActive(cmd) {
        jsonRpcService.close("user");
        localStorage.setItem(this.liveIngTimeKey, '');
        this.updateUserStatusWithIng();

        window.opener.document.location.reload();
        JQ(window).off("beforeunload");
        window.close();


    },
    excelDown(examResultNo) {
        const frm = {id: examResultNo.toString()}
        apiService.post(API_URL_BROADCAST_LIVE_GET_EXAM, frm, (ret) => {
            console.log(ret.info)
            this.excelFileMake(ret.info);

        })

    },
    excelFileMake(info) {
        let datas = JSON.parse(info.problems);

        let tab_text = "<table border='1px'>";
        tab_text = tab_text + '<tr><td colspan="2">' + info.subject + '</td></tr><tr><td colspan="2"></td></tr>';
        let pIdx = 1;
        for (let data of datas) {
            tab_text = tab_text + '<tr><td colspan="2">' + pIdx + '. ' + data.question + '</td></tr>'
            if (data.prbType == 'mchoice') {
                let idx = 1;
                for (let item of data.items) {
                    tab_text = tab_text + '<tr><td>' + idx + '</td><td>' + item.answer + '</td></tr>';
                    idx++;
                }
            }
            tab_text = tab_text + '<tr><td colspan="2"></td></tr>'
        }
        tab_text = tab_text + '</table>';
        let elt = document.createElement("div");
        elt.innerHTML = tab_text;
        document.body.appendChild(elt);

        const wb = window.XLSX.utils.book_new();
        const workSheet = window.XLSX.utils.table_to_sheet(elt.getElementsByTagName("TABLE")[0]);
        document.body.removeChild(elt);

        const fileName = info.subject + '.xlsx';
        const sheetName = info.subject;
        // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
        window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);
        // step 4. 엑셀 파일 만들기
        const wbout = window.XLSX.write(wb, {bookType: 'xlsx', type: 'binary'});
        // step 5. 엑셀 파일 내보내기
        window.saveAs(new Blob([this.s2ab(wbout)], {type: "application/octet-stream"}), fileName);
        this.excelDownActive = false;

    },
    s2ab(s) {
        const buf = new ArrayBuffer(s.length); //convert s to arrayBuffer
        const view = new Uint8Array(buf);  //create uint8array as viewer
        for (var i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF; //convert to octet
        return buf;
    },
}
